import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    reservoir:'',
  },
  getters: {
    getReservoir: function (state) {
      return state.reservoir;
    },
  },
  mutations: {
    setReservoir: function (state, payload) {
      state.reservoir = payload;
    },
  },
  actions: {
  },
  modules: {
  }
})
